.container {
  width: 400px;
  margin: 0 auto;
  text-align: center;
}

.alert-enter {
  transform: translateX(100%);
}

.alert-enter-active {
  transform: translateX(0);
  transition: transform 250ms linear;
}

.alert-exit {
  transform: translateX(0);
}

.alert-exit-active {
  transform: translateX(100%);
  transition: transform 250ms linear;
}
